import Composition from '../data/Glycan';

export default class NetworkBuilder {

    static buildJsonComposition(glycanMap){
        let json = {};
        json.links = [];

        //Node Generation
        json.nodes = Object.keys(glycanMap).map(
            function(key) {
                let sugar = glycanMap[key];
                return {
                    name: sugar.composition.toStringNoZeros(),
                    percentage: sugar.percentage,
                    size: sugar.composition.calculateSize(),
                    unicarbEncoding: sugar.composition.toUnicarbLongEncoding()
                };});

        //Edge Generation
        let i = 0;
        for (let key in glycanMap) {
            const size = glycanMap[key].composition.calculateSize();
            let j = 0;
            for(let innerKey in glycanMap){
                if(size + 1 == glycanMap[innerKey].composition.calculateSize() && this._levenshteinDistance(glycanMap[key].composition.toString(),glycanMap[innerKey].composition.toString()) == 1){
                    let link = {
                        source: i,
                        target: j,
                        difference: this._differenceMonosaccharide(glycanMap[innerKey].composition,glycanMap[key].composition),
                        value: 1
                    };
                    json.links.push(link);
                }
                j++;
            }
            i++;
        }
        return json;
    }


    static _levenshteinDistance(a, b) {
        if(a.length === 0) return b.length;
        if(b.length === 0) return a.length;

        // swap to save some memory O(min(a,b)) instead of O(a)
        if(a.length > b.length) {
            var tmp = a;
            a = b;
            b = tmp;
        }

        var row = [];
        // init the row
        for(let index = 0; index <= a.length; index++){
            row[index] = index;
        }

        // fill in the rest
        for(let i = 1; i <= b.length; i++){
            var prev = i;
            for(var j = 1; j <= a.length; j++){
                var val;
                if(b.charAt(i-1) == a.charAt(j-1)){
                    val = row[j-1]; // match
                } else {
                    val = Math.min(row[j-1] + 1, // substitution
                        prev + 1,     // insertion
                        row[j] + 1);  // deletion
                }
                row[j - 1] = prev;
                prev = val;
            }
            row[a.length] = prev;
        }

        return row[a.length];
    }



    static _differenceMonosaccharide(a,b){

        if(a.Fucose != b.Fucose){
            return 'Fuc';
        }
        if(a.Hex != b.Hex){
            return 'Hex';
        }
        if(a.HexNAc != b.HexNAc){
            return 'HexNAc';
        }
        if(a.NeuAc != b.NeuAc){
            return 'NeuAc';
        }
        if(a.NeuGc != b.NeuGc){
            return 'NeuGc';
        }
        return "";
    }
}


